<template>
  <main class="contact">
    <div class="cm mcm">
    <section class="form_section">
      <div class="page_title">
        <h1>Contact</h1>
      </div>
      <form @submit="send" class="request_form">
        <div class="row">
          <div class="field name" v-if="nameErrors">
            <label for="name">Name <span class="required">Required</span></label>
            <input id="name" v-model="name" type="text" name="name" placeholder="¿" @keyup="checkName">
            <transition name="error_transition">
              <div class="form-control" v-for="nameError in nameErrors" :key="nameError">{{ nameError }}&nbsp;&nbsp;&#8592;</div>
            </transition>
          </div>
          <div class="field email" v-if="emailErrors">
            <label for="email">Email <span class="required">Required</span></label>
            <input id="email" v-model="email" name="email" placeholder="@" @keyup="checkEmail">
            <transition name="error_transition">
              <div class="form-control" v-for="emailError in emailErrors" :key="emailError">{{ emailError }}&nbsp;&nbsp;&#8592;</div>
            </transition>
          </div>
          <div class="field phone">
            <label for="phone">Phone</label>
            <input id="phone" v-model="phone" type="tel" name="phone" placeholder="+">
          </div>
        </div>
        <div class="row">
            <div class="field message" v-if="messageErrors">
              <label for="message">Message <span class="required">Required</span></label>
              <input id="message" v-model="message" type="text" name="message" placeholder="I need a site, logo etc." @keyup="checkMessage">
              <transition name="error_transition">
                <div class="form-control" v-for="messageError in messageErrors" :key="messageError">{{ messageError }}&nbsp;&nbsp;&#8592;</div>
              </transition>
            </div>
        </div>
        <div class="row">
          <button type="submit" :class="[name && email && message ? disClass : 'disable']">
            <span>Send message</span>
          </button>
        </div>
      </form>
    </section>
    </div>
  </main>
</template>

<script>
  import API from '@/api'

  export default {
    name: 'contact',
    data () {
      return {
        name: '',
        phone: '',
        email: '',
        message: '',
        nameErrors: [],
        emailErrors: [],
        messageErrors: [],
        disClass: ''
      }
    },
    methods: {
      send (e) {
        e.preventDefault()
        this.nameErrors = []
        this.emailErrors = []
        this.messageErrors = []
        if (!this.name) {
          this.nameErrors.push('How can we call you?')
        }
        if (!this.email) {
          this.emailErrors.push('We need to know how to contact you.')
        } else if (!this.validEmail(this.email)) {
          this.emailErrors.push('Seems like something is wrong.')
        }
        if (!this.message) {
          this.messageErrors.push('How can we help you?')
        }
        if (!this.nameErrors.length && !this.emailErrors.length && !this.messageErrors.length) {
          return true
        }
        API.sendFeedback({
          name: this.name,
          phone: this.phone,
          email: this.email,
          message: this.message
        }).then(response => {
          // Job's done. Do something
        }).catch(e => {
          // Failed. Show error
        })
      },
      validEmail (email) {
        var re = /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/
        return re.test(email)
      },
      checkName () {
        if (this.nameErrors) {
          this.nameErrors = []
        }
      },
      checkEmail () {
        if (this.emailErrors) {
          this.emailErrors = []
        }
      },
      checkMessage () {
        if (this.messageErrors) {
          this.messageErrors = []
        }
      }
    },
    mounted () {
    }
  }
</script>

<style scoped>
</style>
